<template>
  <div class="row auto" style="background-color: #FFFFFF">
    <div class="col-xs-4 text-center plan" @click = "gotopage('modify-pass-word')">
      <img src="../assets/修改密码.png" class="imgs" alt="">
      <p class="text-center name" style="margin: 10px 0;">修改密码</p>
    </div>
    <div class="col-xs-4 text-center user" @click = "cleandata()">
      <img src="../assets/清空数据.png" class="imgs" alt="">
      <p class="text-center name" style="margin: 10px 0;">清空数据</p>
    </div>
    <div class="col-xs-4 text-center user" @click = "outdata()">
      <img src="../assets/导出数据.png" class="imgs" alt="">
      <p class="text-center name" style="margin: 10px 0;">导出数据</p>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    title: '系统设置',
    data() {
      return {
        model: {}
      }
    },
    methods: {
      gotopage(param) {
        this.$goto(param)
      },
      //头部事件回掉
      back () {
        this.$back()
      },
      // 清空数据
      cleandata(){
        this.$showMessage('此操作为清理数据，无法恢复！', ['confirm', 'cancel']).then((res) => {
          if (res === 'confirm') {
            let result = this.$androidUtil.bzLogic('clearPhoneData', {flag: 0})
            if(result.result == '成功'){
              this.$showAlert('清除数据完成', 'warning', 1000)
            }else{
              this.$showAlert('清除数据失败', 'danger', 1000)
            }
          }
        })
      },
      // 导出数据
      outdata(){
        HostApp.backupDatabase()
      }
    }
  }

</script>
<style scoped>
  .flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .flex-between span {
    padding-right: 10px;
  }
  .flex-full {
    flex: 1;
  }
  .name{
    font: 15px PingFang-SC-Medium;
    color: #666666;
  }
  .imgs{
    width: 60px;
    margin-top: 15px;
  }
  .plan {
    border-bottom: 0.5px solid #EAEAEA;
    border-right: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .user{
    border-bottom: 0.5px solid #EAEAEA;
    border-right: 0.5px solid #EAEAEA;
    border-left: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .safecheck{
    border-bottom: 0.5px solid #EAEAEA;
    border-left: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .appointment{
    border-top: 0.5px solid #EAEAEA;
    border-right: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .search{
    border-top: 0.5px solid #EAEAEA;
    border-right: 0.5px solid #EAEAEA;
    border-left: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .upload{
    border-top: 0.5px solid #EAEAEA;
    border-left: 0.5px solid #EAEAEA;
    height: 120px;
  }


</style>



